<template>
    <div class="shopCart">
        <div class="content" @click="addScroll">
            <div class="content-left">
                <div class="logo-wrapper">
                    <div class="logo active">
                        <i class="icon-shopping_cart"></i>
                    </div>
                    <div class="badge" v-show="compuCount > 0">{{ compuCount }}</div>
                </div>
                <div class="price" :class="{active : total}">￥ {{ total }}</div>
                <div class="desc">另需要配送费￥{{ deliveryPrice }}元</div>
            </div>
            <div class="content-right" :class="{'enough':total >= minPrice}">
                <div class="pay">{{payDesc}}</div>
            </div>
        </div>
        <transition name="transHeight">
        
        <div v-show="total > 0 && show" class="shopcart-list">
            <div class="list-header">
                <h1 class="title">购物车</h1>
                <span class="empty" @click="empty">清空</span>
            </div>
            <div class="list-content" ref="cart">
                <ul>
                    <li v-for="item in food" class="food">
                        <span class="name">{{ item.name }}</span>
                        <div class="price">
                            <span>￥{{ item.price }}</span>
                        </div>
                        <div class="cartcontrol-wrapper">
                            <cartcontrol :food="item"></cartcontrol>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        </transition>
    </div>
</template>

<script>
import cartcontrol from "./cartcontrol";
import Scroll from "better-scroll"; //引入滚动插件

export default {
  props: ["deliveryPrice", "minPrice", "food"],
  data() {
    return {
      show: false
    };
  },
  computed: {
    //总价
    total() {
      let zongjia = 0;
      this.food.forEach(food => {
        zongjia += food.price * food.count;
      });
      return zongjia;
    },
    //总数量
    compuCount() {
      let count = 0;
      this.food.forEach(item => {
        count += item.count;
      });
      return count;
    },
    //最低起送
    payDesc() {
      if (this.total == 0) {
        return `￥${this.minPrice}起送`;
      } else if (this.total < this.minPrice) {
        return `还差${this.minPrice - this.total}起送`;
      } else {
        return "去结算";
      }
    }
  },
  methods:{
      empty(){
          this.food.forEach(val => {
              val.count = 0;
          })
          this.show = false;
      },
      addScroll(){
        this.show = !this.show;
        this.$nextTick(() => {
        new Scroll(this.$refs.cart, {
            click: true
        });
        });
      }
      },
  components: {
    cartcontrol
  },
  mounted() {
  }
};
</script>


