<template>


    <div class="goods">
        <!-- 左侧菜单 -->
        <div class="menu-wrapper" ref="menuWrapper">
            <ul>
                <li class="menu-item" v-for="(item,index) in datas.goods">
                    <span class="text" :class="{'active' : index == 0}" @click="changeRed($event)"> 
                        <span v-if="item.type>0" class="iconMap" :class="icon[item.type]"></span>
                        {{ item.name }} </span>
                </li>
            </ul>
        </div>
        <!-- 右侧点餐 -->
        <div class="foods-wrapper" ref="foodsWrapper">
            <ul>
                <li class="food-list food-list-hook" v-for="item in datas.goods">
                    <h1 class="title">{{item.name}}</h1>
                    <ul>
                        <li v-for="food in item.foods" class="food-item" @click="xiangqingye(food)">
                                <div class="icon">
                                    <img :src="food.icon" width="57" height="57">
                                </div>
                                <div class="content">
                                    <h2 class="name">{{ food.name }}</h2>
                                    <p class="description">{{ food.description }}</p>
                                    <div class="sell-info">
                                        <span class="sellCount">月售{{food.sellCount}}份</span>
                                        <span class="rating">好评率{{ food.rating }}</span>
                                        <div class="price">
                                            <span class="newPrice">￥{{ food.price }}</span>
                                        </div>
                                        <div class="cartcontrol-wrapper">
                                            <cartcontrol :food="food"></cartcontrol>
                                        </div>
                                    </div>
                                </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
        <!-- 下方购物车 -->
        <shopcart :deliveryPrice="datas.seller.deliveryPrice" :minPrice="datas.seller.minPrice" :food="food"></shopcart>
        <!-- 点击进入详情页 -->
       <foodDetail ref="foodDetail" :foodDetails="foodDetails"></foodDetail>   
    </div>


</template>

<script>

import icon from './mods/icon.js'  //引入图标名文件
import Scroll from 'better-scroll'  //引入滚动插件
import shopcart from './mods/shopCart'  
import cartcontrol from './mods/cartcontrol'
import foodDetail from './mods/foodDetail'

    export default {
        props:["datas"],
        data(){
            return {               
                icon : icon,
                red:false,
                foodDetails:{
                    image:''
                }
            }
        },
        methods:{
            xiangqingye(val){
                this.$refs.foodDetail.foodShow();
                this.foodDetails = val;
            },
            changeRed(event){
                let parentNode = event.target.parentNode.parentNode.children;
                for(var i in parentNode){
                    if(parentNode.hasOwnProperty(i)){
                        parentNode[i].firstElementChild.className = 'text';
                        event.target.className = 'text active';
                    }
                }
            }
        },
        computed:{
            food(){
                let arr = []
                this.datas.goods.forEach(goods => {
                    goods.foods.forEach(foods => {
                        if(foods.count > 0){
                            arr.push(foods);
                        }
                    })
                })
                
                return arr;
            }
        },
        mounted(){
            this.$nextTick(()=>{
                new Scroll(this.$refs.foodsWrapper,{
                    click:true
                })
                new Scroll(this.$refs.menuWrapper,{
                    click:true
                })
            })
        },
        components:{
            shopcart,
            cartcontrol,
            foodDetail                                                                                                                                                   
        }
    }
</script>

<style scoped>

.goods .menu-wrapper .menu-item span.active{
    color: red
}

</style>



