<template>
  <div id="app">
    <Header :seller="datas.seller"></Header>
    <div class="tab">
      <div class="tab-item">
          <router-link :to="{name:'goods'}">商品</router-link>
      </div>
      <div class="tab-item">
          <router-link :to="{name:'ratings'}">评价</router-link>
      </div>
      <div class="tab-item">
          <router-link :to="{name:'seller'}">店家</router-link>
          </div>
    </div>
    <!-- 缓存组件 提高性能 -->
    <keep-alive>
      <router-view :datas="datas"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import axios from 'axios'
import Header from './components/mods/header'

export default {
  name: 'App',
  data(){
    return {
      datas:{
        seller:{},
        goods:[],
        active:true
      }
    }
  },
  methods:{
    changeColor(){
      
    }
  },
  mounted(){
      axios.get('./static/data.json').then((res)=>{
          this.datas.seller = res.data.seller;
          this.datas.goods = res.data.goods;
      })
  },
  components:{
    Header
  }
}
</script>

<style scoped>
.tab .tab-item a.router-link-active{
  color: red
}
</style>



