<template>
<transition name="move">    
    <div class="detailWrapper" v-show="showDetail" ref="detail">        
        <div class="foodDetail">
            <div class="back" @click="showDetail = false">
                <i class="icon-arrow_lift"></i>
            </div>
                <img :src="foodDetails.image" width="100%" height="425">
            <div class="info">
                <div class="title">{{ foodDetails.name }}</div>
                <div class="desc">
                    <span>月售{{ foodDetails.sellCount }}</span>
                    <span>好评率{{foodDetails.rating }}%1</span>
                </div>
                <div class="price">￥{{foodDetails.price }}</div>
                <transition name="fade">
                    
                <div class="shopCart" v-show="foodDetails.count == 0 || foodDetails.count == undefined" @click="add">
                    <div class="text">加入购物车</div>
                </div>
                
                </transition>
                <cartcontrol :food="foodDetails" ref="cartcontrol"></cartcontrol>
            </div>
            <div class="divider"></div>
            <div class="desc">
                <div class="title">商品介绍</div>
                <div class="content">{{ foodDetails.info }}</div>
            </div>
            <div class="divider"></div>
            <div class="evaluation">
                <div class="title">商品评价</div>
                <div class="classify">
                    <span v-for="item in classify" class="item" :class="{'active':item.active}" @click="classifyclick(item)">
                        {{item.name}}
                    <span class="count">{{ item.count }}</span>
                        
                    </span>
                </div>
                <div class="switch" @click="flag = !flag"> 
                    <span class="icon-check_circle" :class="{'on':flag }"></span>
                    <span class="text">只看有内容的评价</span>
                </div>
                <div class="evel-list">
                    <ul>
                    <li class="evel" v-for="item in commontss">
                        <div class="userInfo">
                            <div class="time">{{ item.rateTime }} </div>
                            <div class="user">
                                <span>{{ item.userInfo  }}</span>
                                <img :src="item.avatar" width="12" height="12">
                            </div>
                        </div>
                         <div class="content">
                          <span class="icon" :class="item.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
                            <span class="text">{{ item.text }}</span>
                         </div>
                    </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>

</transition>
</template>

<script>
import cartcontrol from "./cartcontrol";//引入购物车加减组件

import Scroll from "better-scroll"; //引入滚动插件
export default {
  props: ["foodDetails"],
  data() {
    return {
      showDetail: false,
      classify: [
        { name: "全部", count: 0, commont: [], active: true },
        { name: "推荐", count: 0, commont: [], active: false },
        { name: "吐槽", count: 0, commont: [], active: false }
      ],
      commonts: [],
      flag: false
    };
  },
  methods: {
    classifyclick(item) {
      this.commonts = item.commont;
      this.classify.forEach(function(item) {
        item.active = false;
      });
      item.active = true;
    },
    updatest() {
      this.$nextTick(() => {
        // this.sc.refresh();
      });
    },
    foodShow() {
      this.showDetail = true;
      this.$nextTick(() => {
        this.commonts = this.foodDetails.ratings;
        this.foodDetails.ratings.forEach(val => {
          this.classify[0].count++;
          this.classify[0].commont.push(val);
          if (val.rateType) {
            this.classify[2].count++;
            this.classify[2].commont.push(val);
          } else {
            this.classify[1].count++;
            this.classify[1].commont.push(val);
          }
        });
      });
      this.$nextTick(() => {
        if (!this.sc) {
          this.sc = new Scroll(this.$refs.detail, {
            click: true
          });
        } else {
          this.sc.refresh();
        }
      });
    },
    add() {
      this.$refs.cartcontrol.addCart();
    }
  },
  computed: {
    commontss() {
      let arr = [];
      this.updatest();
      if (this.flag) {
        this.commonts.forEach(val => {
          if (val.text.length > 0) {
            arr.push(val);
          }
        })
        return arr;
      } else {
        return this.commonts;
      }
    }
  },
  components: {
    cartcontrol
  }
};

</script>


