<template>
    <div class="cartcontrol">
        <transition name="fadeRptate">
        <div class="cart-decrease" @click.stop="subtract" v-show="food.count > 0">
            <span class="icon-remove_circle_outline inner"></span>
        </div>
        
        </transition>
        <div class="cart-count" v-show="food.count > 0">{{ food.count }}</div>
        <div class="cart-add" @click.stop="addCart">
            <i class="icon-add_circle"></i>
        </div>
    </div>
</template>

<script>

import Vue from 'vue'
    
    export default {
        props:['food'],
        methods:{
            addCart(){
                if(!this.food.count){
                    Vue.set(this.food,'count',0);
                    Vue.set(this.food,'active',true)
                }
                this.food.count++;
            },
            subtract(){
                this.food.count <= 0? this.food.count = 0: this.food.count--; 
            }

        }
    }


</script>



