<template>
    <div class="ratingsWrapper" ref="ratingsWrapper">
        <div class="ratings-content">
            <div class="info">
                <div class="mark">
                    <div class="num">{{datas.seller.score }}</div>
                    <div class="text">综合评分</div>
                    <div class="contrast">高于周边商家{{ datas.seller.rankRate }}%</div>
                </div>
                <div class="stars">
                    <div class="serviceScore">
                        <span class="text">服务态度</span>
                        <star :size="36" :score="datas.seller.serviceScore"></star>
                        <span class="num"> {{ datas.seller.serviceScore }} </span>
                    </div>
                    <div class="foodScore">
                        <span class="text">商品评分</span>
                        <star :size="36" :score="datas.seller.serviceScore"></star>
                        <span class="num"> {{ datas.seller.serviceScore }} </span>
                    </div>
                </div>
            </div>
            <div class="divider"></div>
            <div class="evaluation">
                <div class="classify">
                    <span class="item" v-for="(item,index) in classify" @click="evel(item)" :class="{ 'active': item.active}" >
                        {{  item.name }}
                        <span class="count"> {{ item.count }} </span>
                    </span>
                </div>
                <div class="evel-list">
                    <ul>
                    <li class="evel" v-for="item in comment">
                        <div class="avatar">
                            <img :src="item.avatar" width="28" height="28" alt="用户">
                        </div>
                        <div class="content">
                            <div class="user">
                                <span class="name">{{ item.username }}</span>
                                <span class="rateTime">{{ item.rateTime }}</span>
                            </div>
                            <div class="star-wrapper">
                                <star :size="24" :score="item.score"></star>
                                <span class="deliveryTime">{{item.deliveryTime  }}分钟送达</span>
                            </div>
                            <div class="text">{{ item.text }}</div>
                            <div class="recommend">
                                <span class="icon" :class="item.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
                                <span class="dish" v-for="dish in item.recommend">{{dish}}</span>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
import axios from "axios"; //引入axios获取数据
import star from "./mods/star";
import Scroll from "better-scroll"; //引入滚动插件

export default {
  props: ["datas"],
  data() {
    return {
      ratings: "",
      classify: [
        { name: "全部", count: 0, active: true, comment: [] },
        { name: "推荐", count: 0, active: false, comment: [] },
        { name: "吐槽", count: 0, active: false, comment: [] }
      ],
      evalflag: true,
      comments: []
    };
  },
  mounted() {
    axios.get("./static/data.json").then(res => {
      this.ratings = res.data.ratings;
      this.comments = res.data.ratings;
      this.ratings.forEach(val => {
        this.classify[0].count++;
        this.classify[0].comment.push(val);
        if (val.rateType) {
          this.classify[2].count++;
          this.classify[2].comment.push(val);
        } else {
          this.classify[1].count++;
          this.classify[1].comment.push(val);
        }
      });
      this.$nextTick(() => {
        this.sc = new Scroll(this.$refs["ratingsWrapper"], {
          click: true
        });
      });
    });
  },
  methods: {
    evel(item) {
      this.classify.forEach(val => {
        val.active = false;
      });
      item.active = true;
      this.comments = item.comment;
    }
  },
  computed: {
    comment() {
      if (this.evelflag) {
        //为真表示要看有内容的评价
        let arr = []; //保存有内容的评价
        this.comments.forEach(val => {
          //遍历所点击类型的所有评论
          if (val.text.length > 0) {
            // 如果遍历到评论文字长度不为0的则表示这是需要展示的评论
            arr.push(val);
          }
        })
        return arr;
      } else {
        return this.comments;
      }
    }
  },
  components: {
    star
  }
};
</script>


